.footer {
  background-color: #051644;

  &__holder {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 90px;
    padding: 5px;

    @include max-screen(768px) {
      height: 56px;
    }

    img {
      margin-right: 8px;

      @include max-screen(768px) {
        max-width: 24px;
      }
    }

    span {
      font-size: 20px;
      line-height: 24px;
      font-weight: 600;
      color: $white;

      @include max-screen(768px) {
        font-size: 16px;
        line-height: 24px;
      }
    }
  }

}