.header {
  position: fixed;
  right: 0;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 999;
  padding: 24px 0 10px;
  transform: translate(0, 0);
  transition: all 0.2s ease-in-out;

  @include max-screen(1200px) {
    padding: 32px 0 0px;
  }

  &__holder {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    @include max-screen(1200px) {
      padding: 0 25px;
      position: relative;
      flex-direction: row;
      justify-content: space-between;

      align-items: flex-start;
      margin: 0;
    }
  }

  &.is-hidden {
    transform: translate(0, -160px);
    background-color: #051644;
  }

  &.is-inverse {
    @include min-screen(1200px) {
      background-color: $white;
    }

    .logo {
      @include min-screen(1200px) {
        background-color: $accent;
      }
    }

    .nav {
      ul {
        li {
          a {
            @include min-screen(1200px) {
              color: $black;
            }
          }
        }
      }
    }
  }
}


.logo {
  height: 64px;
  width: 64px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: $white;
  margin-bottom: 22px;

  @include max-screen(1200px) {
    margin-bottom: 0;
  }
}

.logo-mini {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: $white;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 32px;
  left: 24px;

  @include min-screen(1200px) {
    display: none;
  }
}

.nav {

  @include max-screen(1200px) {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
    height: 100vh;
    width: 100%;
    background-color: rgba(0, 0, 0, 0);
    backdrop-filter: blur(0);
    transform: translate(-100%, 0);
    transition: all 0.1s linear;
  }

  ul {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: center;

    @include max-screen(1200px) {
      justify-content: flex-start;
      flex-direction: column;
      background: linear-gradient(179.67deg, #041644 -13.04%, #00756e 99.72%);
      box-shadow: 5px 0px 8px rgba(0, 0, 0, 0.25);
      height: 100%;
      padding-left: 48px;
      padding-top: 120px;
      max-width: 294px;
      width: 100%;
      overflow: auto;
    }

    li {
      margin: 0 7px;
      display: flex;
      position: relative;

      @include max-screen(1366px) {
        margin: 0 4px;
      }

      @include max-screen(1200px) {
        width: 100%;
        margin: 0;
      }

      &:after,
      &:before {
        content: '';
        position: absolute;
        width: 1px;
        height: 100%;
        background-color: $accent;
        transform: scaleY(0);
        transition: transform 0.3s;

        @include max-screen(1200px) {
          display: none;
        }
      }

      &:before {
        bottom: 0;
        left: 0;
        transform-origin: bottom left;
      }

      &:after {
        top: 0;
        right: 0;
        transform-origin: top right;
      }

      &:first-child {
        margin-left: 0;

        @include max-screen(1200px) {
          margin: 0;
        }
      }

      &:last-child {
        margin-right: 0;
      }

      &:hover {

        &:before,
        &:after {
          transform: scaleY(1);
        }

        a {

          &:before,
          &:after {
            transform: scaleX(1);
          }
        }
      }

      a {
        font-family: $sub-font;
        text-decoration: none;
        font-size: 14px;
        line-height: 20px;
        font-weight: 600;
        display: inline-block;
        padding: 10px 8px;
        letter-spacing: 2px;
        color: $white;
        position: relative;
        // overflow: hidden;
        text-transform: uppercase;


        @include max-screen(1440px) {
          letter-spacing: 0.5px;
        }

        @include max-screen(1366px) {
          letter-spacing: 0;
        }

        @include max-screen(1200px) {
          width: 100%;
          letter-spacing: 2px;
          padding: 0;
          margin: 0 0 32px 0;
        }

        &:after,
        &:before {
          content: '';
          position: absolute;
          width: 100%;
          height: 1px;
          background-color: $accent;
          transform: scaleX(0);
          transition: transform 0.3s;
        }

        &:before {
          top: 0;
          left: 0;
          transform-origin: top left;

          @include max-screen(1200px) {
            display: none;
          }
        }

        &:after {
          bottom: 0;
          right: 0;
          transform-origin: bottom right;

          @include max-screen(1200px) {
            left: -16px;
            top: 48%;
            width: 8px;
            height: 1px;
            background-color: $white;
            transform: none;
          }
        }
      }
    }
  }

  &.is-open {
    transform: translate(0, 0);
    background-color: rgba(0, 0, 0, 0.4);
    backdrop-filter: blur(2px);
  }
}

.burger {
  height: 36px;
  width: 36px;
  display: none;


  @include max-screen(1200px) {
    display: block;
    margin-top: 18px;
  }

  &__holder {

    span {
      display: block;
      width: 32px;
      height: 4px;
      border-radius: 2px;
      background-color: $white;
      transition: 0.3s ease;
      position: relative;

      &:after,
      &:before {
        content: '';
        position: absolute;
        left: 0;
        background-color: $white;
        height: 4px;
        border-radius: 2px;
      }

      &:after {
        width: 20px;
        top: 12px;

      }

      &:before {
        width: 100%;
        top: 24px;
      }
    }
  }

  &.is-open {}
}

.btn-close {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: $transp;
  border: none;
  position: absolute;
  top: 34px;
  left: 240px;
  padding: 0;

  @include min-screen(1200px) {
    display: none;
  }

  &:after,
  &:before {
    content: '';
    position: absolute;
    background-color: $white;
    width: 30px;
    height: 2px;
    transform-origin: center;
  }

  &:after {
    transform: rotate(45deg);
  }

  &:before {
    transform: rotate(-45deg);
  }
}