/* styles */
.wrapper {
	display: flex;
	flex-direction: column;
	min-height: 100vh;
	width: 100%;
	max-width: 100%;
	margin: 0 auto;
	overflow-x: hidden;
	background: linear-gradient(179.67deg, #00756E -13.04%, #041644 99.72%);

	// background: linear-gradient(179.67deg, #00756E -13.04%, #041644 99.72%);
}

.header,
.footer {
	flex: 0 0 auto;
}

.main-page {
	flex: 1 0 auto;
	overflow: hidden;
	// padding-top: 150px;
}

.container {
	max-width: 1420px;
	width: 100%;
	padding: 0 10px;
	margin: 0 auto;
	color: $txt-color;

	@include max-screen(768px) {
		padding: 0 16px;
	}
}

//Text style

.section-title {
	font-weight: 700;
	font-size: 42px;
	line-height: 50px;
	text-align: center;
	color: $white;
	// margin-bottom: 28px;
	max-width: 70%;
	width: 100%;
	margin: 0 auto 28px;

	@include max-screen(1460px) {
		font-size: 38px;
		line-height: 46px;
	}

	@include max-screen(1366px) {
		font-size: 34px;
		line-height: 42px;
	}

	@include max-screen(1200px) {
		font-size: 30px;
		line-height: 38px;
	}

	@include max-screen(1024px) {
		max-width: 90%;
	}

	@include max-screen(768px) {
		font-size: 20px;
		line-height: 30px;
		max-width: 100%;
		margin: 0 auto 16px;
	}

	&._accent-color {
		color: $accent;
	}

	span {
		color: $white;
	}
}

//Hide elem 
.visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	bottom: 0;
	padding: 0;
	clip: rect(0 0 0 0);
	overflow: hidden;
}

.js-no-scroll {
	overflow: hidden;

	@include min-screen(1200px) {
		overflow: auto;
	}
}