// =====Basic colors=====
$white: #fff;
$black: #000;
$gray: #ccc;
$transp: transparent;
$accent: #41F1F0;
// Custom colors...
$text: $white;

// =====Typography base=====
$main-font: 'Montserrat',
Arial,
sans-serif;
$sub-font: 'Inter',
sans-serif;
$font-weight: 400;
$font-size: 16px;
$line-height: 1.5;
$txt-color: $black;

// =====Typography buttons=====
$btn-ff: $main-font;
$btn-fz: $font-size;
$btn-lh: 20px;
$btn-fw: $font-weight;
$btn-color: $txt-color;
$btn-bgc: $transp;
$btn-radius: 0px;