.hero {
  padding: 223px 0 370px;
  position: relative;
  background: linear-gradient(180deg, #016A6A 0%, #03495B 100%);

  @include max-screen(1460px) {
    padding: 180px 0 200px;
  }

  @include max-screen(1200px) {
    padding: 180px 0 140px;
  }

  @include max-screen(1024px) {
    padding: 140px 0 65px;
  }

  &:after,
  &:before {
    content: '';
    position: absolute;
    background-repeat: no-repeat;

    @include max-screen(1024px) {
      display: none;
    }
  }

  &:after {
    top: 300px;
    left: 0;
    height: 680px;
    width: 900px;
    background-size: contain;
    background-image: url(../img/hero-decor-01.svg);

    @include max-screen(1670px) {
      top: 200px;
      left: 0;
      height: 580px;
      width: 700px;
    }

    @include max-screen(1466px) {
      height: 380px;
      width: 600px;
    }

  }

  &:before {
    top: 0%;
    right: -21%;
    width: 949px;
    height: 1233px;
    background-size: auto;
    background-image: url(../img/hero-decor-02.svg);

    @include max-screen(1670px) {
      right: -29%;
      width: 749px;
      height: 1000px;
    }

    @include max-screen(1466px) {
      right: -16%;
      width: 549px;
      height: 700px;
    }
  }

  &__container {}

  &__ctn {
    max-width: 720px;

    width: 100%;
    text-align: left;
    padding-right: 12px;

    @include max-screen(1366px) {
      max-width: 620px;
    }

    @include max-screen(1024px) {
      margin: 0 auto 32px;
      max-width: 80%;
      text-align: center;
      padding: 0;
    }

    @include max-screen(768px) {
      max-width: 100%;
    }
  }

  &__title {
    font-weight: 800;
    font-size: 200px;
    line-height: 151px;
    text-transform: uppercase;
    color: $white;
    text-shadow: -2px -2px 3px #FFFFFF;
    text-align: left;
    margin-bottom: 32px;
    position: relative;
    color: rgba(255, 255, 255, 1);
    text-shadow: -3px 3px 3px rgb(0 0 0 / 90%);
    -webkit-background-clip: text;
    -moz-background-clip: text;
    background-clip: text;



    &:after {
      content: '';
      position: absolute;
      display: block;
    }

    @include max-screen(1536px) {
      font-size: 160px;
      line-height: 125px;
    }

    @include max-screen(1460px) {
      font-size: 140px;
      line-height: 110px;
    }

    @include max-screen(1366px) {
      font-size: 120px;
      line-height: 90px;
    }

    @include max-screen(1024px) {
      font-size: 80px;
      line-height: 60px;
      margin-bottom: 16px;
      text-align: center;
    }

    br {
      display: none;

      @include max-screen(460px) {
        display: block;
      }
    }
  }

  &__descr {
    font-size: 18px;
    line-height: 27px;
    font-weight: 500;
    color: $white;

    @include max-screen(1024px) {
      font-size: 14px;
      line-height: 21px;
    }

    p {
      &:last-child {
        margin: 0;
      }
    }
  }

  &__illustr {
    display: flex;
    position: absolute;
    right: 5%;
    top: 200px;
    transform: scale(0);
    transform-origin: center;
    transition: all 0.5s ease-in-out;

    @include max-screen(1670px) {
      max-width: 700px;
    }

    @include max-screen(1536px) {
      max-width: 660px;
    }

    @include max-screen(1460px) {
      max-width: 610px;
      right: 2%;
    }

    @include max-screen(1366px) {
      max-width: 580px;
    }

    @include max-screen(1200px) {
      max-width: 470px;
    }

    @include max-screen(1024px) {
      position: static;
      margin: 0 auto;
    }

  }

  &__anim {}

  &.js-animation {
    .hero__illustr {
      transform: scale(1);
    }
  }
}