.section-app {
  padding: 90px 0;
  background: url(../img/decor-elem-03.svg), linear-gradient(180deg, #04274C 0%, #051B47 100%);
  background-repeat: no-repeat;
  background-position: left top;
  scroll-margin-top: 100px;

  @include max-screen(1024px) {
    padding: 75px 0;
  }

  @include max-screen(768px) {
    background-image: none;
  }

  &__holder {
    display: flex;
    align-items: center;
    justify-content: center;

    @include max-screen(1200px) {
      flex-direction: column;
    }
  }

  &__descr {
    font-size: 20px;
    line-height: 30px;
    font-weight: 500;
    color: $white;
    margin-right: 53px;
    max-width: 545px;
    padding-right: 10px;

    @include max-screen(1200px) {
      text-align: center;
      margin: 0 auto 32px;
      max-width: 86%;
    }

    @include max-screen(768px) {
      font-size: 14px;
      line-height: 21px;
      padding: 0;
    }

    @include max-screen(768px) {
      max-width: 100%;
    }

    p {
      &:last-child {
        margin: 0;
      }
    }
  }

  &__illustr {
    position: relative;
    transform: translate(100%, 0);
    opacity: 0;
    transition: all 0.3s ease-in-out;

    .deco-box {
      position: absolute;
      top: 0;
      left: 22%;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 89px;
      height: 80px;
      background-color: $transp;
      box-shadow: 0 0 0 rgba(0, 0, 0, 0);
      box-shadow: -7.15089px 14.3018px 53.6316px rgba(82, 114, 139, 0.24);
      border-radius: 8px;


      @include max-screen(568px) {
        width: 69px;
        height: 60px;
        top: 25px;
        left: 25%;
      }

      @include max-screen(460px) {
        width: 59px;
        height: 50px;
        top: 10px;
        left: 20%;
      }

      @include max-screen(360px) {
        width: 49px;
        height: 40px;
      }

      &:after {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        bottom: -100%;
        background-color: #144662;
        opacity: 0;
        border-radius: 8px;
        transition: all 0.6s linear;

      }

      img {
        position: relative;
        z-index: 10;

        @include max-screen(460px) {
          width: 60%;
        }
      }
    }

    .gear-dark {
      position: absolute;
      left: 13%;
      top: 45%;
      animation: rotation 10s infinite reverse linear;

      @include max-screen(568px) {
        max-width: 60px;
      }

      @include max-screen(460px) {
        max-width: 42px;
        left: 16%;
      }

      @include max-screen(360px) {
        max-width: 32px;
      }
    }

    .gear-light {
      position: absolute;
      right: 16%;
      bottom: 9%;
      animation: rotation 10s infinite linear;

      @include max-screen(568px) {
        max-width: 60px;
      }

      @include max-screen(460px) {
        max-width: 42px;
        right: 18%;
        bottom: 12%;
      }

      @include max-screen(360px) {
        max-width: 32px;
      }
    }

    &.js-animation {
      opacity: 1;
      transform: translate(0, 0);

      .deco-box {

        &:after {
          opacity: 1;
          bottom: 0;
        }
      }
    }
  }

  .section-title {
    font-size: 42px;
    line-height: 50px;
    font-weight: 700;
    margin-bottom: 68px;

    @include max-screen(1460px) {
      font-size: 38px;
      line-height: 46px;
      margin-bottom: 48px;
    }

    @include max-screen(1366px) {
      font-size: 32px;
      line-height: 40px;
      margin-bottom: 38px;
    }

    @include max-screen(1200px) {
      font-size: 28px;
      line-height: 38px;
      margin-bottom: 28px;
    }

    @include max-screen(460px) {
      font-size: 20px;
      line-height: 30px;
      margin-bottom: 24px;
    }
  }
}

// Animation keyframes block 
@keyframes rotation {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(-359deg);
  }
}