.section-platform {
  padding: 100px 0 48px;
  background: url(../img/bg-point-down.svg),
    linear-gradient(180deg, #03495B 0%, #04274C 100%);
  background-position: center top;
  background-size: cover;

  @include max-screen(1024px) {
    padding: 65px 0 48px;
  }

  @include max-screen(768px) {
    padding: 48px 0;
  }

  &__subtitle {
    font-weight: 500;
    font-size: 18px;
    line-height: 27px;
    color: $white;
    text-align: center;
    max-width: 58%;
    margin: 0 auto 40px;

    @include max-screen(1024px) {
      max-width: 76%;
      font-size: 16px;
      line-height: 24px;
    }

    @include max-screen(768px) {
      max-width: 100%;
      font-size: 14px;
      line-height: 21px;
      margin: 0 auto 24px;
    }

  }

  .section-title {
    margin: 0 auto 24px;

    @include max-screen(768px) {
      margin: 0 auto 16px;
    }
  }
}

.info-list {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  &__items {
    border: 1px solid $white;
    border-radius: 10px;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5);
    flex-basis: 374px;
    margin: 0 20px 42px;
    position: relative;
    transform: translate(0, 50%);
    opacity: 0;
    transition-property: transform, opacity;
    transition-timing-function: ease-in-out;

    @include max-screen(460px) {
      flex-basis: auto;
      margin: 0 auto 16px;

    }

    &:after {
      content: '';
      position: absolute;
      z-index: 1;
      top: 50%;
      right: 50%;
      transform: translate(50%, -50%);
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center;
      height: 140px;
      width: 140px;

      @include max-screen(768px) {
        top: 16px;
        right: unset;
        left: 25px;
        transform: translate(0, 0);
      }
    }

    &.icon-posts {
      &:after {
        background-image: url(../img/icon-posts.svg);

        @include max-screen(768px) {
          width: 57px;
          height: 57px;
        }
      }
    }

    &.icon-video {
      &:after {
        background-image: url(../img/icon-video.svg);

        @include max-screen(768px) {
          width: 56px;
          height: 53px;
        }
      }
    }

    &.icon-control {
      &:after {
        background-image: url(../img/icon-control.svg);

        @include max-screen(768px) {
          width: 56px;
          height: 65px;
        }
      }
    }

    &.icon-short {
      &:after {
        background-image: url(../img/icon-shortVideo.svg);

        @include max-screen(768px) {
          width: 56px;
          height: 56px;
        }
      }
    }

    &.icon-history {
      &:after {
        background-image: url(../img/icon-history.svg);

        @include max-screen(768px) {
          width: 56px;
          height: 56px;
        }
      }
    }

    &:first-child {
      transition-duration: 0.2s;
    }

    &:nth-child(2) {
      transition-duration: 0.4s;
    }

    &:nth-child(3) {
      transition-duration: 0.6s;
    }

    &:nth-child(4) {
      transition-duration: 0.8s;
    }

    &:last-child {
      transition-duration: 1s;
    }
  }

  &__descr {
    color: $white;
    text-align: center;
    padding: 32px 20px;
    position: relative;
    z-index: 10;
  }

  &__subject {
    font-size: 20px;
    line-height: 30px;
    font-weight: 700;
    color: $white;
    margin-bottom: 16px;
  }

  p {
    font-size: 16px;
    line-height: 24px;
    font-weight: 500;

    @include max-screen(768px) {
      font-size: 14px;
      line-height: 21px;
    }

    &:last-child {
      margin: 0;
    }
  }

  &.js-animation {
    .info-list__items {
      transform: translate(0, 0);
      opacity: 1;
    }
  }
}