.section-web {
  padding-top: 45px;
  position: relative;
  scroll-margin-top: 100px;
  background-image: url(../img/bg-map.svg),
    linear-gradient(180deg, #051B47 0%, #04274C 100%);
  background-repeat: no-repeat;
  background-position: right top;
  background-size: auto;

  @include max-screen(1200px) {
    padding-bottom: 0;
  }

  @include max-screen(1024px) {
    background-image: none;
    background: linear-gradient(180deg, #051B47 0%, #04274C 100%);
  }

  &__heading {
    margin-bottom: 155px;
    text-align: center;

    @include max-screen(1466px) {
      margin-bottom: 135px;
    }

    @include max-screen(1360px) {
      margin-bottom: 115px;
    }

    @include max-screen(1200px) {
      margin-bottom: 95px;
    }

    @include max-screen(1024px) {
      margin-bottom: 75px;
    }

    @include max-screen(768px) {
      margin-bottom: 32px;
    }
  }

  &__slogan {
    font-size: 150px;
    line-height: 113px;
    font-weight: 800;
    color: $accent;
    text-transform: uppercase;
    text-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);

    @include max-screen(1536px) {
      font-size: 130px;
      line-height: 100px;
    }

    @include max-screen(1466px) {
      font-size: 110px;
    }

    @include max-screen(1360px) {
      font-size: 90px;
      line-height: 70px;
    }

    @include max-screen(1200px) {
      font-size: 70px;
    }

    @include max-screen(1024px) {
      font-size: 55px;
      line-height: 82px;
    }

    @include max-screen(360px) {
      font-size: 46px;
      line-height: 62px;
    }
  }

  &__wrapper {}

  &__layout {
    padding-right: 10px;
    padding-left: 10px;

    &.content-platform {
      padding-bottom: 329px;
      position: relative;

      @include max-screen(1536px) {
        padding-bottom: 280px;
      }

      @include max-screen(1466px) {
        padding-bottom: 240px;
      }

      @include max-screen(1360px) {
        padding-bottom: 180px;
      }

      @include max-screen(1024px) {
        padding-bottom: 120px;
      }

      @include max-screen(768px) {
        padding-bottom: 100px;
      }

      &:after {
        content: '';
        position: absolute;
        top: 175px;
        left: 0;
        background-image: url(../img/decor-elem-04.svg);
        background-repeat: no-repeat;
        background-position: left center;
        background-size: cover;
        height: 610px;
        width: 540px;

        @include max-screen(1024px) {
          display: none;
        }
      }

      .section-web__illustr {
        // margin-right: 40px;
        max-width: 711px;
        height: 423px;
        position: absolute;
        left: -10%;
        top: -50%;

        @include max-screen(1466px) {
          left: 0;
          transform: translateY(25%);
        }

        @include max-screen(1200px) {
          position: static;
          transform: translate(0, 0);
          justify-content: center;
          height: auto;
        }

        .icon-peer {
          transform: translate(-100%, 0);
          opacity: 0;
          transition: all .3s ease-in-out;

          @include max-screen(1466px) {
            max-width: 80%;
          }

          @include max-screen(1024px) {
            max-width: 70%;
          }

          @include max-screen(768px) {
            max-width: 100%;
          }
        }

        &.js-animation {
          .icon-peer {
            transform: translate(0%, 0);
            opacity: 1;
          }
        }
      }

      .section-web__descr {
        margin-left: auto;

        @include max-screen(1200px) {
          text-align: center;
          max-width: 90%;
          margin: 0 auto;
        }

        // @include max-screen(1024px) {
        //   margin: 0 auto 10px;
        //   text-align: center;
        // }
      }
    }

    &.content-points {
      padding-bottom: 310px;
      scroll-margin-top: 180px;
      // background: linear-gradient(180deg, #051B47 0%, #04274C 100%);
      background: linear-gradient(180deg, rgb(5 27 71 / 20%) 0, #04274c 100%);

      @include max-screen(1536px) {
        padding-bottom: 280px;
      }

      @include max-screen(1466px) {
        padding-bottom: 240px;
      }

      @include max-screen(1360px) {
        padding-bottom: 180px;
      }

      @include max-screen(1024px) {
        padding-bottom: 120px;
      }

      @include max-screen(768px) {
        padding-bottom: 120px;
      }

      .section-web__illustr {
        position: absolute;
        top: 20%;
        right: -30%;

        @include max-screen(1860px) {
          right: -22%;
        }

        @include max-screen(1760px) {
          right: -17%;
        }

        @include max-screen(1630px) {
          right: -10%;
        }

        @include max-screen(1460px) {
          right: -3%;
        }

        @include max-screen(1265px) {
          right: 3%;
        }

        @include max-screen(1200px) {
          position: static;
          flex-direction: column;
          align-items: center;
          width: 100%;
        }

        .score-system {
          position: relative;
          z-index: 1;
          transition-property: transform;
          transition-timing-function: ease-out;

          @include max-screen(1860px) {
            max-width: 260px;
          }

          @include max-screen(1760px) {
            max-width: 240px;
          }

          @include max-screen(1630px) {
            max-width: 220px;
          }

          @include max-screen(1460px) {
            max-width: 200px;
          }

          @include max-screen(1265px) {
            max-width: 180px;
          }

          @include max-screen(1200px) {
            max-width: 260px;
          }

          @include max-screen(768px) {
            max-width: 145px;
          }

          &:first-child {
            z-index: 30;

            @include max-screen(1200px) {
              margin-left: -200px;
            }

            @include max-screen(460px) {
              margin-left: -150px;
            }
          }

          &:nth-child(2) {
            margin: 0 42px;
            z-index: 20;
            transform: translate(-100%, 0);
            transition-duration: 0.3s;

            @include max-screen(1630px) {
              margin: 0 22px;
            }

            @include max-screen(1200px) {
              margin: 0 0 0 80px;
            }

            @include max-screen(768px) {
              margin: 0;
            }

            @include max-screen(460px) {
              margin-top: -16px;
            }
          }

          &:last-child {
            z-index: 10;
            transform: translate(-200%, 0);
            transition-duration: 0.5s;

            @include max-screen(1200px) {
              margin: 0 0 0 360px;
            }

            @include max-screen(768px) {
              margin: 0 0 0 200px;
            }

            @include max-screen(460px) {
              margin: -16px 0 0 150px;
            }
          }
        }

        &:after {
          content: '';
          position: absolute;
          left: 0;
          top: -52%;
          background-image: url(../img/decor-elem-01.svg);
          background-repeat: no-repeat;
          background-size: contain;
          width: 506px;
          height: 450px;

          @include max-screen(1200px) {
            left: 30%;
            top: 40%;
          }

          @include max-screen(1024px) {
            display: block;
            left: 80%;
            top: 55%;
            transform: translateX(-50%);
            width: 100%;
            height: 330px;
          }

          @include max-screen(768px) {
            width: 270px;
            top: 42%;
            left: 54%;
            transform: translateX(-50%) rotate(-40deg);
          }

          @include max-screen(460px) {
            top: 44%;
            left: 52%;
          }

          @include max-screen(380px) {
            top: 52%;
            left: 50%;
            transform: translateX(-50%) rotate(-10deg);
          }

        }

        &.js-animation {
          .score-system {
            &:first-child {}

            &:nth-child(2) {
              transform: translate(0, 0);
            }

            &:last-child {
              transform: translate(0, 0);
            }
          }
        }
      }

      .section-web__descr {
        margin-right: auto;

        @include max-screen(1200px) {
          margin: 0 auto 34px;
          text-align: center;
          max-width: 90%;
        }

        @include max-screen(768px) {
          max-width: 100%;
        }
      }

      ._layout-reverse {
        .section-web__caption {
          @include max-screen(1200px) {
            text-align: center;
          }
        }

        .section-web__descr {
          @include max-screen(1200px) {
            text-align: center;
          }
        }
      }
    }

    &.content-token {
      padding-bottom: 365px;
      position: relative;
      scroll-margin-top: 220px;
      background: linear-gradient(180deg, #04274C 0%, #033E56 100%);

      @include max-screen(1536px) {
        padding-bottom: 280px;
      }

      @include max-screen(1466px) {
        padding-bottom: 240px;
      }

      @include max-screen(1360px) {
        padding-bottom: 180px;
      }

      @include max-screen(1024px) {
        padding-bottom: 120px;
      }

      @include max-screen(768px) {
        padding-bottom: 120px;
      }

      @include max-screen(460px) {
        padding-right: 0;
        padding-left: 0;
        padding-bottom: 150px;
        padding: 0 0 120px 0;
      }

      .section-web__holder {
        @include max-screen(1200px) {
          flex-direction: column-reverse;
        }

        .bg-decor {
          display: none;

          @include max-screen(1200px) {
            display: block;
            width: 100%;
          }

        }
      }

      &:after {
        content: '';
        position: absolute;
        top: -100px;
        left: 0;
        background-image: url(../img/decor-elem-02.svg);
        background-repeat: no-repeat;
        background-size: auto;
        max-width: 896px;
        width: 0;
        height: 409px;
        transition: width 1s ease-in-out;

        @include max-screen(1200px) {
          display: none;
        }
      }

      .section-web__illustr {
        margin-right: 366px;
        position: absolute;
        left: -50px;
        top: -50px;
        z-index: 10;
        transform: translate(-200%, 0);
        transition: transform 0.6s linear;

        @include max-screen(1837px) {
          left: -14px;
        }

        @include max-screen(1700px) {
          left: 7%;
        }

        @include max-screen(1563px) {
          left: 12%;
        }

        @include max-screen(1460px) {
          max-width: 240px;
          left: 18%;
          top: -30px;
        }

        @include max-screen(1366px) {
          max-width: 220px;
          left: 22%;
          top: -18px;
        }

        @include max-screen(1248px) {
          left: 25%;
        }

        @include max-screen(1200px) {
          top: 40%;
          max-width: 330px;
          margin: 0;
          left: 38%;
        }

        @include max-screen(1024px) {
          top: 38%;
          left: 35%;
        }

        @include max-screen(768px) {
          max-width: 200px;
          left: 38%;
        }

        @include max-screen(660px) {
          max-width: 180px;
          left: 36%;
        }

        @include max-screen(560px) {
          max-width: 157px;
          left: 36%;
          top: 46%;
        }

        @include max-screen(480px) {
          left: 32%;
        }

        @include max-screen(380px) {
          left: 30%;
          top: 50%;
        }

        @include max-screen(360px) {
          left: 28%;
        }

        @include max-screen(340px) {
          left: 26%;
        }

        &.js-animation {
          transform: translate(0, 0);

          .icon-coin {
            transform: rotate(0);
          }
        }
      }

      .icon-coin {
        transition: transform 0.6s linear;
        transform: rotate(-180deg);
      }

      .section-web__descr {
        margin-left: auto;
        position: relative;
        z-index: 10;

        @include max-screen(460px) {
          margin-bottom: 18px;
          padding-right: 10px;
          padding-left: 10px;
        }

        @include max-screen(380px) {
          margin-bottom: 26px;
        }
      }

      &.js-animation {
        &:after {
          width: 100%;
        }
      }
    }

    &.content-statistic {
      padding-bottom: 180px;
      scroll-margin-top: 200px;
      position: relative;
      background: linear-gradient(180deg, #033E56 0%, #04274C 100%);

      &:after {
        content: '';
        position: absolute;
        background-image: url(../img/decor-elem-05.svg);
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
        width: 457px;
        height: 667px;
        top: -70%;
        right: 0;

        @include max-screen(1200px) {
          display: none;
        }
      }

      @include max-screen(1460px) {
        padding-bottom: 160px;
      }

      @include max-screen(1366px) {
        padding-bottom: 120px;
      }

      .section-web__descr {
        margin-right: 140px;

        @include max-screen(1460px) {
          margin-right: auto;
        }

        @include max-screen(460px) {
          margin-bottom: 24px;
        }
      }

      .section-web__illustr {
        position: relative;
        z-index: 10;

        &.js-animation {
          .icon-statistic {
            transform: translate(0, 0);
            opacity: 1;
          }

          .decor-box {
            &:after {
              bottom: 0;
              opacity: 1;
            }
          }
        }
      }

      .icon-statistic {
        position: relative;
        transform: translate(100%, 0);
        opacity: 0;
        transition: all .3s ease-in-out;

        .decor-box {
          position: absolute;
          bottom: 0;
          left: 33%;
          display: flex;
          align-items: center;
          justify-content: center;
          background-color: $transp;
          width: 89px;
          height: 81px;
          border-radius: 8px;
          box-shadow: 0 0 0 rgba(0, 0, 0, 0);

          img {
            position: relative;
            z-index: 10;
          }

          &:after {
            content: '';
            position: absolute;
            background-color: $accent;
            left: 0;
            bottom: -100%;
            width: 100%;
            height: 100%;
            border-radius: 8px;
            opacity: 0;
            border-radius: 8px;
            box-shadow: -16.0895px 24.1342px 36.6483px rgba(19, 194, 150, 0.24);
            transition: all 0.5s ease-in-out;
          }
        }
      }
    }

    &.content-application {
      padding-bottom: 180px;
      scroll-margin-top: 260px;
      position: relative;
      background: linear-gradient(180deg, #04274C 0%, #033E56 100%);

      @include max-screen(1460px) {
        padding-bottom: 140px;
      }

      @include max-screen(1366px) {
        padding-bottom: 120px;
      }

      &:after {
        content: '';
        position: absolute;
        bottom: -68%;
        right: 0;
        left: 0;
        background-image: url(../img/bg-rain-dots.svg);
        background-repeat: repeat-x;
        background-size: contain;
        height: 634px;
        width: 100%;

        @include max-screen(1200px) {
          display: none;
        }
      }

      .section-web__descr {
        @include max-screen(1200px) {
          margin: 0 auto 100px;
        }

        @include max-screen(360px) {
          margin: 0 auto 80px;
        }
      }

      .section-web__illustr {
        margin-right: 31px;

        @include max-screen(1200px) {
          margin-right: 0;
        }

        &.js-animation {

          .decor-lg,
          .decor-md,
          .decor-xs {
            transform: scale(1);
          }

          .decor-wifi {
            transform: translate(0, -30%) scale(1);
            animation: flicker 2s linear 2;
          }
        }
      }

      .icon-platform {
        position: relative;

        @include max-screen(580px) {
          max-width: 86%;
          margin: 0 auto;
        }

        .decor-wifi {
          position: absolute;
          top: -28%;
          left: 48%;
          opacity: 1;
          transition-property: transform;
          transition-duration: 1s;
          transition-timing-function: ease-in-out;
          transform: translate(0, 225%) scale(0);

          @include max-screen(1200px) {
            top: -15%;
          }

          @include max-screen(480px) {
            left: 45%;
          }

          @include max-screen(425px) {
            max-width: 46px;
            left: 47%;
            top: -10%;
          }
        }

        .decor-lg {
          position: absolute;
          top: 22%;
          left: 36%;
          transition-property: transform;
          transform: scale(0);
          transition-duration: 0.3s;
          transition-timing-function: ease-in-out;

          @include max-screen(580px) {
            max-width: 150px;
            left: 36%;
            top: 29%;
          }

          @include max-screen(540px) {
            max-width: 120px;
            left: 40%;
            top: 36%;
          }

          @include max-screen(480px) {
            max-width: 100px;
            left: 40%;
            top: 35%;
          }

          @include max-screen(425px) {
            max-width: 90px;
            left: 40%;
            top: 30%;
          }

          @include max-screen(360px) {
            max-width: 80px;
          }
        }

        .decor-md {
          position: absolute;
          top: 18%;
          left: 39%;
          transition-property: transform;
          transform: scale(0);
          transition-duration: 0.5s;
          transition-timing-function: ease-in-out;

          @include max-screen(580px) {
            max-width: 130px;
            left: 39%;
            top: 24%;
          }

          @include max-screen(540px) {
            max-width: 100px;
            left: 43%;
            top: 32%;
          }

          @include max-screen(480px) {
            max-width: 80px;
            left: 43%;
            top: 30%;
          }

          @include max-screen(425px) {
            max-width: 70px;
            left: 43%;
            top: 28%;
          }

          @include max-screen(360px) {
            max-width: 60px;
            left: 44%;
          }
        }

        .decor-xs {
          position: absolute;
          top: 8%;
          left: 44%;
          transition-property: transform;
          transform: scale(0);
          transition-duration: 0.7s;
          transition-timing-function: ease-in-out;

          @include max-screen(580px) {
            max-width: 90px;
            top: 15%;
            left: 44%;
          }

          @include max-screen(540px) {
            max-width: 70px;
            top: 22%;
            left: 47%;
          }

          @include max-screen(480px) {
            max-width: 50px;
            top: 21%;
            left: 47%;
          }

          @include max-screen(425px) {
            max-width: 46px;
          }

          @include max-screen(360px) {
            max-width: 40px;
            left: 48%;
          }
        }
      }
    }

    &.content-spot {
      padding-bottom: 90px;
      scroll-margin-top: 200px;
      background: linear-gradient(180deg, #033E56 0%, #025360 100%);

      .section-web__illustr {

        @include max-screen(480px) {
          &:after {
            content: '';
            position: absolute;
            top: 0;
            background-image: url(../img/decor-elem-05.svg);
            background-repeat: no-repeat;
            background-size: contain;
            background-position: center;
            max-width: 375px;
            width: 100%;
            height: 547px;
          }
        }

        &.js-animation {
          .icon-btc {
            transform: translate(0, 0);
            opacity: 1;
          }
        }
      }

      .section-web__descr {
        margin-right: 104px;
      }

      .icon-btc {
        transform: translate(100%, 0);
        opacity: 0;
        transition: all .3s ease-in-out;

        @include max-screen(1200px) {
          max-width: 82%;
          margin: 0 auto;
        }

        @include max-screen(480px) {
          position: relative;
          z-index: 10;
        }
      }
    }

  }

  &__holder {
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 1200px;
    width: 100%;
    margin: 0 auto;
    position: relative;

    @include max-screen(1200px) {
      flex-direction: column-reverse;
    }

    &._layout-reverse {
      flex-direction: row-reverse;

      @include max-screen(1200px) {
        flex-direction: column-reverse;
      }

      .section-web__descr {
        text-align: left;

        @include max-screen(1200px) {
          text-align: center;
          margin: 0 auto 34px;
        }
      }

      .section-web__caption {
        text-align: left;

        @include max-screen(1200px) {
          text-align: center;
        }
      }

    }
  }

  &__illustr {
    display: flex;
    position: relative;
  }

  &__descr {
    max-width: 48%;
    width: 100%;
    font-size: 18px;
    line-height: 27px;
    font-weight: 500;
    color: $white;
    text-align: right;

    @include max-screen(1200px) {
      text-align: center;
      max-width: 90%;
      margin: 0 auto 34px;
    }

    @include max-screen(768px) {
      font-size: 14px;
      line-height: 21px;
    }

    @include max-screen(460px) {
      max-width: 100%;
    }

    p {
      &:last-child {
        margin: 0;
      }
    }
  }

  &__caption {
    text-align: right;
    font-size: 32px;
    line-height: 48px;
    font-weight: 700;
    color: $white;
    margin-bottom: 24px;

    @include max-screen(1200px) {
      text-align: center;
      font-size: 28px;
      line-height: 38px;
    }

    @include max-screen(768px) {
      font-size: 24px;
      line-height: 28px;
    }

    @include max-screen(460px) {
      font-size: 20px;
    }
  }

  .section-title {
    font-weight: 800;
    font-size: 96px;
    line-height: 72px;
    text-transform: uppercase;
    text-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
    max-width: 100%;
    margin-bottom: 14px;

    @include max-screen(1536px) {
      font-size: 80px;
      line-height: 52px;
    }

    @include max-screen(1440px) {
      font-size: 76px;
      line-height: 52px;
    }

    @include max-screen(1366px) {
      font-size: 56px;
    }

    @include max-screen(1200px) {
      font-size: 50px;
      line-height: 34px;
    }

    @include max-screen(1024px) {
      font-size: 44px;
      margin: 0;
    }

    @include max-screen(768px) {
      font-size: 36px;
    }

    @include max-screen(360px) {
      font-size: 32px;
    }
  }
}


@keyframes flicker {
  0% {
    opacity: 0.4;
  }

  5% {
    opacity: 0.5;
  }

  10% {
    opacity: 0.6;
  }

  15% {
    opacity: 0.85;
  }

  25% {
    opacity: 0.5;
  }

  30% {
    opacity: 1;
  }

  35% {
    opacity: 0.1;
  }

  40% {
    opacity: 0.25;
  }

  45% {
    opacity: 0.5;
  }

  60% {
    opacity: 1;
  }

  70% {
    opacity: 0.85;
  }

  80% {
    opacity: 0.4;
  }

  90% {
    opacity: 0.5;
  }

  100% {
    opacity: 1;
  }
}